<template>
  <div class="hospital">
    <hos-options @search="search" ref="options"/>
    <hos-table @search="search" :hosTotalSize="totalSize" :loading="loading" :data="data" :pageShow="pageShow" ref="table"/>
  </div>
</template>

<script>
import HosOptions from './controls/hosOptions'
import HosTable from './controls/hosTable'
import queryString from "querystring"

export default {
  name: 'hospital',
  data() {
    return {
      data:[], //table数据
      pageShow: false,  //页码切换是否显示
      totalSize: 0, //一共多少条数据
      loading: false
    }
  },
  components: {
    HosOptions,
    HosTable
  },
  mounted(){
    this.search();
  },
  methods:{
    search(){  //搜索
      this.loading = true;
      const optionParams = this.$refs.options.getData();
      const tableParams = this.$refs.table.getData();
      let params = this.lodash.assign({}, optionParams, tableParams);
      const param = queryString.stringify(params || {});
      this.axios.get(`/hospital?${param}`).then((res)=>{
        this.loading = false;
        this.data = res.data.data.list;
        this.totalSize = res.data.data.totalPage;
        this.pageShow = this.data.length > 20;
        this.dataInit();
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    dataInit(){ //数据初始化
      // this.data.map((item) => {
      //   item.address = item.address || item.province + item.city + item.country
      //   item.dept = item.dept || `${item.dept1}-${item.dept2}`;
      // })
    }
  }
}
</script>

