<template>
  <el-container>
    <el-header class="header">
      <div class="left">
        <div>
          医院名称：<el-input class="com-input" placeholder="请输入医院名称" v-model="comName" />
          医院等级：<el-select v-model="gradeValue" class="com-input" placeholder="全部">
            <el-option v-for="item in hosGrade" :key="item.id" :label="item.dValue" :value="item.dKey" />
          </el-select>
        </div>
        <div class="area">
          <span class="text">地区：</span>
          <user-address ref="address"/>
        </div>
      </div>
      <div class="right">
        <el-button type="primary" icon="el-icon-search" class="btn" @click="search">查询</el-button>
        <el-button type="primary" @click="addCom" class="btn">新增医院</el-button>
      </div>
    </el-header>
  </el-container>
</template>
<script>
import { hosLevelList } from "data/listBody"
import UserAddress from "components/address";
export default {
  name: "hosOptions",
  data() {
    return {
      hosGrade: hosLevelList, //医院等级
      comName: "",
      gradeValue: ""
    };
  },
  components: {
    UserAddress
  },
  methods: {
    addCom() {
      this.$router.push("hospitalAdd?type=add");
    },
    search(){
      this.$emit("search");
    },
    getData(){
      let params = {
        level: this.gradeValue, //医院等级
        name: this.comName, //医院名称
      }
      return this.lodash.assign({},params,this.$refs.address.getData());
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./../../../../assets/less/color.less");
.header {
  display: flex;
  text-align: left;
  border-bottom: 1px solid @border-gray;
  height: inherit !important;
  padding-bottom: 20px;
  .area {
    margin-top: 20px;
    display: flex;
    align-items: center;
    .text {
      display: inline-block;
      text-indent: 2em;
      min-width: 80px;
    }
  }
  .com-input {
    width: 210px;
    margin-right: 20px;
  }
  .right {
    display: flex;
    align-items: center;
    .btn {
      margin-left: 20px;
    }
  }
}
</style>