<template>
  <el-container>
    <el-main v-loading="loading">
      <el-table :data="hospitalList">
        <el-table-column prop="id" label="医院ID" />
        <el-table-column prop="name" label="医院名字" />
        <el-table-column prop="otherName" label="医院别名" />
        <el-table-column prop="level" label="医院等级" />
        <el-table-column prop="address" label="医院地区" />
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="delMsg(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box" v-show='pageShow'>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="size"
          layout="total, prev, pager, next"
          :total="totalSize">
        </el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>确认删除该条信息？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
// import { hospitalInfo } from "../../../../data/hosNetwork";
export default {
  name: "hosTable",
  props:['pageShow', 'data', 'loading', 'hosTotalSize'],
  data() {
    return {
      hospitalList: [],
      // hospitalList: Array(8).fill(hospitalInfo),
      dialogVisible: false,
      id:'',
      currentPage: 1,
      totalSize: this.hosTotalSize,
      size: 20
    };
  },
  methods: {
    handleClick(params) {
      this.id = params.id;
      this.$router.push(`hospitalAdd?type=edit&id=${this.id}`);
    },
    delMsg(params) {
      this.id = params.id;
      this.dialogVisible = true;
    },
    sure(){
      this.axios.delete(`/hospital/${this.id}`).then(()=>{
        this.hospitalList = this.hospitalList.filter(item=>{
          return item.id !== this.id
        })
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
      this.dialogVisible = false
    },
    getData(){
      return {
        size: this.size,
        page: this.currentPage -1
      }
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.$emit('search')
      console.log(`当前页: ${val}`);
    }    
  },
  watch:{
    data(newValue){
      this.hospitalList = newValue;
    }
  }
};
</script>
<style lang="less" scoped>
</style>